<template>
<div class="add_address">
	<div class="header"><i class="el-icon-arrow-left" @click="goBack"></i>选择收件地址</div>
	<div class="add_address_main">
		<div class="user_address">
			<ul>
				<li v-for="(item, index) in address" @click="selectAddress(index)" :key="index" :class="{ 'active': item.active}">
					<div class="dogo"><i></i></div>
                    <div class="info">
                    	<span class="n">{{item.name}}</span>
                    	<span class="p">{{item.phone}}</span>
                    	<span class="a">{{item.address}}</span>
                    </div>
				</li>
			</ul>
		</div>
	</div>
	<div class="address_save" @click="address_selected">保 存</div>
</div>
</template>
<script>
import cookies from "js-cookie";
export default {
  props: [],
  data() {
    return {
      address: [],
      aid: "",
      select: false
    };
  },
  created() {
    var self = this;
    if (cookies.get("f_userid")) {
      this.$store.dispatch("getUserAddress", {
        id: cookies.get("f_userid"),
        cb: function(data) {
          if (data.body.code == 200) {
            self.address = data.body.data;
            console.log(self.address);
          }
        }
      });
    }
  },
  methods: {
    selectAddress(index) {
      var self = this;
      this.$nextTick(() => {
        this.address.forEach(function(item, i) {
          if (i == index) {
            if (!item.active) {
              item.active = true;
            } else {
              item.active = false;
            }
            self.aid = item._id;
            self.select = item.active;
          } else {
            item.active = false;
          }

          self.$forceUpdate();
        });
      });
    },
    address_selected() {
      var self = this;
      this.$store.dispatch("addressSelect", {
        aid: this.aid,
        select: this.select,
        cb: function(data) {
          if (data.body.code == 200) {
            self.$router.push({ path: "/cart" });
          }
        }
      });
    },
    goBack() {
      window.history.go(-1);
    }
  }
};
</script>
<style>
.add_address {
  display: flex;
  flex-direction: column;
  height: 100%;
  background: #f2f2f2;
}
.add_address .header {
  background: #fff;
  height: 48px;
  line-height: 48px;
  color: #333;
  text-align: center;
  font-size: 20px;
  border-bottom: 1px solid #ddd;
  margin-bottom: 10px;
  position: relative;
}
.add_address .header i {
  position: absolute;
  left: 0px;
  top: 0px;
  width: 38px;
  height: 48px;
  line-height: 48px;
}
.add_address .add_address_main {
  background: #fff;
  border-top: 1px solid #ddd;
}
.add_address .user_address {
  background: #fff;
}
.add_address .user_address ul li {
  display: flex;
  align-items: center;
  border-bottom: 1px solid #e6e6e6;
}

.add_address .user_address .dogo {
  width: 30px;
  height: 68px;
  cursor: pointer;
}
.add_address .user_address .dogo i {
  display: block;
  width: 15px;
  height: 15px;
  border: 1px solid #9e9e9e;
  border-radius: 50%;
  margin: 27px auto 0;
  font-size: 10px;
  color: #fff;
  position: relative;
}
.add_address .user_address .dogo i:before {
  top: -3px;
  position: absolute;
}
.add_address .user_address li.active .dogo i {
  background: #e93b3d;
}
.add_address .user_address .info {
  flex: 1;
}
.add_address .user_address .info .p {
  margin: 0 10px;
}
.address_save {
  border-radius: 10px;
  width: 90%;
  margin: 20px auto;
  background: #ff5c5c;
  color: #fff;
  text-align: center;
  height: 42px;
  line-height: 42px;
  text-align: center;
}
</style>


